import React, { Component } from 'react';
// import IosArrowRoundDown from 'react-ionicons/lib/IosArrowRoundDown';
// import { BrowserRouter, Link } from 'react-router-dom';

class Banner extends Component {

    render() {
        return (
            <div className="parallax">
                <img src={require("../images/weight2.jfif")} alt="" className="mainBanner" />
                {/* <h1 className="quote">THE WORLD OF TECHNOLOGY</h1> */}
                <div className="basicInfo">
                    <ul>
                        <li id="firstInfo">Weight Loss</li>
                        <li>Weight Gain</li>
                        <li>Functional Traning</li>
                        <li>TRX</li>
                        <li>Bulking</li>
                    </ul>
                </div>
            </div>
        );
    }
}

export default Banner;