import React, { Component } from 'react';
import Navbar from './navbar';
import Footer from './footer';
import serviceSource from './servicesSource';

class Services extends Component {
    style = {
        color: "white",
        fontSize: "20px",
        marginLeft: "20px"
    }
    render() {
        return (
            <div>
                <Navbar />
                <div className="parallax">
                    <img src={require("../images/services.jpg")} alt="" className="mainBanner" />
                    {/* <div className="servicebasicInfo">
                        <div style={this.style}>
                            <h3 className="servicesTitle">Our Services</h3>
                            At Ducklin, we believe digital technologies will drastically change the
                            way businesses function in the future. Our offerings are focused
                            towards equipping organizations offer exceptional customer
                            experience through our technology and usability services.
                        </div>
                    </div> */}
                </div>
                {/* <div className="col-12 serviceInfo">
                    <p className="serviceInfoDetails">
                        <p className="one">From traditional to emerging
                        technologies, we cater to a wide range of
                        digital services that enhance the processes and
                        experience for </p>
                        <p className="two">our customers. We partner with funded startups,
                        established businesses and Independent Software Developers (ISV’s)
                        to deliver</p>
                        <p className="three">world-class technology and usability services.</p><hr />
                    </p>
                </div> */}
                <div className="serviceProvided">
                    <div className="ServicemainContainer">
                        <h1 className="ServidecardTitle">SERVICES</h1>
                        <div className="slideRow">
                            {
                                serviceSource.map((infoData) => {
                                    return (
                                        <div className="card" id="slideDetails" style={{ width: '18rem' }} key={infoData.id}>
                                            <img className="card-img-top imageAlter" src="..." alt="Card image cap" src={infoData.img} />
                                            <div className="card-body">
                                                <h5 className="card-title">{infoData.title}</h5><hr />
                                                <p className="card-text">{infoData.content}</p>
                                                {/* <a href="#" class="btn btn-primary">{infoData.button}</a> */}
                                            </div>
                                        </div>
                                    )
                                })
                            }
                        </div>
                    </div>
                </div>
                <Footer />
            </div>
        );
    }
}

export default Services;